*{padding:0;margin:0;box-sizing:border-box}body{margin:0;font-family:'Jost', sans-serif}.menu__list{width:100%;color:#fff;font-size:1.5rem;background-color:#1d1c1e;position:absolute;bottom:0;left:0;z-index:-1;transition:bottom 0.3s ease-in-out}@media (min-width: 992px){.menu__list{z-index:0;font-size:14px;color:#1d1c1e;position:relative;background-color:transparent;list-style:none;display:flex;justify-content:flex-end}}.menu__list--open{bottom:-12.5rem}.menu__list-item{padding:1rem;text-align:right;text-transform:uppercase;letter-spacing:0.2rem;position:relative;transition:color 0.1s linear}.menu__list-item:hover{cursor:pointer;color:#e7816b}@media (min-width: 992px){.menu__list-item{padding:0;margin:0 1rem}.menu__list-item::after{content:'';width:0;height:1px;position:absolute;bottom:-0.25rem;left:0;transition:width 0.3s linear;background-color:#e7816b}.menu__list-item:hover::after{width:100%}}

*{padding:0;margin:0;box-sizing:border-box}body{margin:0;font-family:'Jost', sans-serif}.header{height:6rem;display:flex;background-color:#fff;justify-content:center;align-items:center;position:-webkit-sticky;position:sticky;top:0}@media (min-width: 992px){.header{height:10rem}}.header__nav{width:100%;max-width:1024px;height:100%;display:flex;justify-content:space-between;align-items:center;padding:0 1rem;position:relative;background-color:#fff}.header__logo{height:1.5rem}.header__menu{border:none;background-color:transparent}@media (min-width: 992px){.header__menu{display:none}}

